'use client';
import { useState, useEffect } from 'react';
import { supabase } from '../lib/supabaseClient';
import { FaPlus } from 'react-icons/fa'; // Assurez-vous d'avoir react-icons installé

export default function AddItemPage({ table, onItemAdded }) {
  const [title, setTitle] = useState('');
  const [file, setFile] = useState(null);
  const [message, setMessage] = useState('');
  const [items, setItems] = useState([]);
  const [user, setUser] = useState(null);

  useEffect(() => {
    fetchItems();
    const storedUser = localStorage.getItem('user');
    if (storedUser) setUser(JSON.parse(storedUser));
  }, []);

  const fetchItems = async () => {
    const { data } = await supabase.from(table).select('*').order('created_at', { ascending: false });
    if (data) setItems(data);
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    let image_url = null;
    if (file) {
      const fileName = `${Date.now()}-${file.name}`;
      const { error: uploadError } = await supabase.storage.from('uploads').upload(fileName, file);
      if (uploadError) {
        setMessage("❌ Erreur lors de l'upload");
        return;
      }
      image_url = supabase.storage.from('uploads').getPublicUrl(fileName).data.publicUrl;
    }

    const { error } = await supabase.from(table).insert([{ title, image_url }]);
    if (error) {
      setMessage("❌ Erreur en base");
    } else {
      setMessage('✅ Cours ajouté !');
      setTitle('');
      setFile(null);
      fetchItems();

      if (typeof onItemAdded === 'function') {
        onItemAdded();
      }
    }
  };

  return (
    <div style={{ padding: '2rem' }}>
      {user?.role === 'admin' && (
        <form
  onSubmit={handleSubmit}
  style={{
    padding: '2.5rem',
    backgroundColor: '#1f2937',
    color: 'white',
    borderRadius: '20px',
    border: '1px solid #374151',
    boxShadow: '0 10px 30px rgba(0,0,0,0.4)',
    display: 'flex',
    flexDirection: 'column',
    gap: '2rem',
    width: '600%',
  }}
>
  <h2
    style={{
      textAlign: 'center',
      fontSize: '2rem',
      fontWeight: 'bold',
      color: '#e0e7ff',
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center',
      gap: '0.5rem',


    }}
  >
    <FaPlus /> Ajouter un cours
  </h2>

  <div
    style={{
      display: 'grid',
      gridTemplateColumns: '1fr 1fr',
      gap: '1rem',
      
    }}
  >
    <div>
      <label style={{ fontWeight: '600', marginBottom: '0.5rem', display: 'block' }}>
        Titre du cours
      </label>
      <input
        type="text"
        placeholder="Ex: React JS"
        value={title}
        onChange={(e) => setTitle(e.target.value)}
        required
        style={{
          padding: '0.75rem',
          borderRadius: '10px',
          width: '70%',
          border: '1px solid #6b7280',
          backgroundColor: '#111827',
          color: 'white',
          
        }}
      />
    </div>

    <div>
      <label style={{ fontWeight: '600', marginBottom: '0.5rem', display: 'block',          marginLeft:'20%', }}>
        Image du cours
      </label>
      <input
        type="file"
        accept="image/*"
        onChange={(e) => setFile(e.target.files[0])}
        required
        style={{
          padding: '0.75rem',
          borderRadius: '10px',
          width: '70%',
          border: '1px solid #6b7280',
          backgroundColor: '#111827',
          color: 'white',
          marginLeft:'20%',
        }}
      />
    </div>
  </div>

  <button
    type="submit"
    style={{
      padding: '1rem 2rem',
      backgroundColor: '#3b82f6',
      color: 'white',
      fontWeight: 'bold',
      borderRadius: '12px',
      border: 'none',
      cursor: 'pointer',
      alignSelf: 'center',
      fontSize: '1rem',
      width: '70%',
      transition: 'background-color 0.3s ease',
    }}
  >
    ➕ Ajouter
  </button>

  {message && (
    <p style={{ textAlign: 'center', color: '#10b981', fontWeight: 'bold' }}>{message}</p>
  )}
</form>


      )}
    </div>
  );
}
